
.tel-num {
  display: flex;
  align-items: center;
  width: 142px;
  text-decoration: none;
  &__num {
    position: relative;
    top: 100%;
    margin-right: 20px;
    font-size: 12px;
    font-weight: 700;
    font-style: normal;
    color: #fff;
    white-space: nowrap;
    visibility: hidden;
    transition: top 250ms;
    @include tablet() {
      display: none;
    }
  }
  &__img {
    @include iconsSet;
  }
  &:hover &__num {
    visibility: visible;
    top: 0;
  }
  @include tablet() {
    width: auto;
  }
}
